<template lang="pug">
#IntroducingEagle.eg-theme-agrume
  .eg-slideshow
    slide(enter='fadeIn' leave='bounceOutLeft')
      .center.frontpage
        h1 Internship
        img(src='https://zeekdoc.com/build/images/logo_zeekdoc.svg')
        h4 Find the right doctor Book instantly
        eg-triggered-message(:trigger='slideTimer >= 2',
                            :duration='6', position='top right',
                            enter='bounceInRight', leave='bounceOutRight')
          p Next:
          img.control-schema(src='./assets/controlsNext.svg')
          p Previous:
          img.control-schema(src='./assets/controlsPrev.svg')

    slide(:steps=6, enter='bounceInRight' leave='bounceOutDown')
      h3
        //- .inline(class='animated infinite pulse heart')
        | Zeekdoc is?
        //- .inline(class='animated infinite pulse heart')
      .center
        eg-transition(enter='bounceInLeft' leave='bounceOutLeft')
          p(v-if="step >= 2")
            <b>Zeekdoc</b> is a platform helping you find nearby medical specialists and book an appointment online.
        eg-transition(enter='bounceInRight' leave='bounceOutRight')
          p(v-if="step >= 3")
            <b>Zeekcod</b> can find nearby doctor
        eg-transition(enter='bounceInLeft' leave='bounceOutLeft')
          p(v-if="step >= 4")
            <b>Zeekcod</b> can choose preferred one
        eg-transition(enter='bounceInLeft' leave='bounceOutLeft')
          p(v-if="step >= 5")
            <b>Zeekcod</b> can medical specialists
    //- slide(:steps=2, enter='bounceInRight' leave='bounceOutDown')
    //-   h3
    //-     | ‡πÑ‡∏õ‡∏ù‡∏∂‡∏Å‡∏á‡∏≤‡∏ô‡πÑ‡∏î‡πâ‡∏ó‡∏≥‡∏≠‡∏∞‡πÑ‡∏£‡∏ö‡πâ‡∏≤‡∏á ?
      //- .center
      //-   eg-transition(enter='bounceInLeft' leave='bounceOutLeft')
      //-     p(v-if="step >= 2")
      //-       img(src='./assets/icons/basic_share.svg')
      //-   eg-transition(enter='bounceInRight' leave='bounceOutRight')
      //-     p(v-if="step >= 3")
      //-       <b>Node.js</b> supports components and transitions out of the box
      //-   eg-transition(enter='bounceInLeft' leave='bounceOutLeft')
      //-     p(v-if="step >= 4")
      //-       <b>Eagle.js</b> adds navigation logics, scalable components, etc.

    slide(:steps=8, enter='bounceInDown')
      h3 What's Framework use in working

      eg-transition(enter='fadeIn' leave='fadeOut')
        .quarter(v-if='step >=2')
          img(src='https://vuejs.org/images/logo.png' width="80px")
          h4 Vue
          p Coolest frontend framework

      eg-transition(enter='fadeIn' leave='fadeOut')
        .quarter(v-if='step >=3')
          img(src='https://www.krui3.com/wp-content/uploads/2018/04/AWS-S3.jpeg' width="150px" hight="100px")
          h4 AWS
          p Clound, Severless, Api and DB

      eg-transition(enter='fadeIn' leave='fadeOut')
        .quarter(v-if="step >= 4")
         img(src='https://upload.wikimedia.org/wikipedia/commons/thumb/d/d9/Node.js_logo.svg/2000px-Node.js_logo.svg.png' width="150px" hight="100px")
         h4 Node js
         p create api and backend

      eg-transition(enter='fadeIn' leave='fadeOut')
        .quarter(v-if="step >= 5")
          img(src='https://cdn.freebiesupply.com/logos/large/2x/laravel-1-logo-png-transparent.png' width="150px" hight="100px")
          h4 Laravel
          p Php Framework

      eg-transition(enter='fadeIn' leave='fadeOut')
        eg-modal(v-if='step >= 6')
          h3 STAND UP MEETING:
          .center
            img(:src="preloadedImages['standUpmeeting']",
                :style="{height: '10em'}")

    slide(:steps=4, leave='fadeOut')
      eg-transition(enter='flipInX')
        h3 New knowledge and work experience in a real work

      eg-transition(enter='flipInX')
        p Continuous Integration on Bitbucket pipelines:

      eg-transition(enter='flipInX')
        eg-code-block(lang='html').
          image: lambci/lambda:build-nodejs6.10
          pipelines:
            default:
              - step:
                  deployment: production
                  script:
                    - npm install
                    - npm install node-lambda -g
                    - node-lambda deploy -a $AWS_ACCESS_KEY -s $AWS_SECRET_KEY -o $AWS_ROLE -r $AWS_REGION -n $AWS_LAMBDA_FUNCTION_NAME --excludeGlobs "bitbucket-pipelines.yml"
                  caches:
                    - node

    slide(:steps=5, enter='bounceInRight' leave='bounceOutDown')
      h3
        | Responsible work
      .center
        eg-transition(enter='bounceInLeft' leave='bounceOutLeft')
          p(v-if="step >= 2")
            img(src='https://scontent.fbkk7-3.fna.fbcdn.net/v/t1.0-9/48396734_2228948207149828_4706979093478899712_o.jpg?_nc_cat=105&_nc_eui2=AeEYbuvfEJFdpShcGSeeaqWV0_D_A51eKUMnWGIChDLu2-hVxB_ArWwoULT6syKBMQnFkyRrlR1uiteGFoIso5W1QQBOo-i9-S3iCaXxKxIyQw&_nc_ht=scontent.fbkk7-3.fna&oh=283bd4ae908e54bf8e2aaaa89f16cea7&oe=5C8AFFA5')
        eg-transition(enter='bounceInRight' leave='bounceOutRight')
          p(v-if="step >= 3")
            <b>Setting page</b> config data to web frontend

    slide(:steps=5, enter='bounceInLeft' leave='bounceOutDown')
      h3
        | Responsible work
      .center
        eg-transition(enter='bounceInLeft' leave='bounceOutLeft')
          p(v-if="step >= 2")
            img(src='https://scontent.fbkk7-3.fna.fbcdn.net/v/t1.0-9/48384122_2228949937149655_220908194011021312_o.jpg?_nc_cat=108&_nc_eui2=AeES1_zKhlK6DZDBEw5sfT-zqHdYSUO6XdN-svTnk9Wscxnnv88B15l9OqzAR9SnZTBc0TjjLmiYm3c9odfoEISnKXP0H2Jp_Mz5zC0MyKaDPw&_nc_ht=scontent.fbkk7-3.fna&oh=8bb3af8f355787137d7fe676205fc70a&oe=5C9EEA23')
        eg-transition(enter='bounceInRight' leave='bounceOutRight')
          p(v-if="step >= 3")
            <b>Galley page</b> Add image to web Frontend.

    slide(enter='zoomIn', leave='fadeOut')
      h3 That's all work!
      p.center Thank you
      .center
        img.computerkid.shadowbox(src='https://quotesblog.net/wp-content/uploads/2018/09/chris-patt-thank-you-gif.gif')
      p.center.
        I want to be frontend developer<br />
        is welcome to see my contribute <a href="https://github.com/teerapat1739" target='_blank'> the repo</a>.

    slide(enter='fadeIn')
      h3 Thank you !
      p This slideshow's so fresh, it has end credits.
      end-credits(:username='username')
</template>

<script>
import eagle from 'eagle.js'

export default {
  mixins: [eagle.slideshow],
  infos: {
    title: 'Internship',
    description: 'Present internship',
    path: 'intern'
  },
  components: {
  },
  data: function () {
    return {
      username: 'Tracy',
      preference: 'baby bunnies',
      preferences: [
        {
          label: 'baby bunnies',
          text: 'Baby bunnies'
        },
        {
          label: 'fluffy puppies',
          text: 'Fluffy puppies'
        },
        {
          label: 'funny kitties',
          text: 'Funny kitties'
        }
      ],
      slideCategories: {
        themes: {
          show: true,
          name: 'Theming'
        },
        slideReuse: {
          show: true,
          name: 'Slide reuse'
        },
        interactivity: {
          show: true,
          name: 'Interactivity'
        }
      },
      preloadedImages: {
        computerKid: 'https://i.imgur.com/AAlntwU.gif',
        youRightNow: 'https://i.imgur.com/DFBTj0a.gif',
        bretagne: 'https://i.imgur.com/rYkJ6I8.jpg',
        forrestRoad: 'https://i.imgur.com/hxTMFZW.jpg',
        starrySky: 'https://i.imgur.com/yO2ivoD.jpg',
        cityBokeh: 'https://i.imgur.com/kmmHith.jpg',
        darkWoods: 'https://i.imgur.com/FL9mwpd.jpg',
        pushButton: 'https://i.imgur.com/KaAyvZ5.gif',
        standUpmeeting: 'https://blog.salesflare.com/wp-content/uploads/2017/05/1FkMNMf835c1UFaNcGkAz5Q.gif'
      }
    }
  },
  methods: {
    message: function (name) {
      return {
        'baby bunnies': 'Yeeeeah my favorite too !',
        'fluffy puppies': 'Wow so original.',
        'funny kitties': 'Good for you ' + this.username + '.',
        'Theming': 'Ok ' + this.username + ', whatever.',
        'Slide reuse': 'Seriously ' + this.username + ' you <em>want</em> to see this.',
        'Interactivity': 'Well that\'s this slide, ' + this.username +
                         '. <br /> A bit too late to unsee it, heh ?'
      }[name]
    }
  }
}
</script>

<style lang='scss'>
@import 'node_modules/eagle.js/dist/themes/agrume/agrume';
#IntroducingEagle {
  .frontpage {
    img {
      height: 7em;
    }
    img.control-schema {
      width: 8em;
      height: 3em;
    }
  }
  .heart {
    width: 1em;
    height: 0.8em;
    margin-left: 0.1em;
    margin-right: 0.1em;
    background-image: url('./assets/heart.svg');
    background-position: center center;
    background-repeat:  no-repeat;
    background-size: contain;
  }

  .quarter {
    text-align: center;
    p {
      margin-top: 0;
      text-align: center;
    }
    h4 {
      margin-top: 0;
      margin-bottom: 0
    }
  }
  .boredYet {
    p {
      margin-bottom: 0.3em;
      margin-top: 1.3em;
    }
    .button {
      border: none;
    }
    .button.prev {
      float: left;
    }
    .button.next {
      float: right;
    }
  }
  img.computerkid {
    height: 6.5em;
  }

  a {
    color: black;
  }

  .parentWindow {
    border: solide 1px red;
  }
}
</style>
