import Vue from 'vue'
import Router from 'vue-router'
import App from './App'
import Home from './Home'
import Homepage from './views/Homepage.vue'

import Eagle, { Options } from 'eagle.js'
import 'eagle.js/dist/eagle.css'
import hljs from 'highlight.js'
import VueTypedJs from 'vue-typed-js'

import slideshows from './slideshows/slideshows.js'

/* eslint-disable no-new */

Vue.use(Eagle)
Vue.use(Router)
Vue.use(VueTypedJs)

Vue.config.productionTip = false
Options.hljs = hljs

let routes = []
slideshows.list.forEach(function (slideshow) {
  routes.push({
    path: '/' + slideshow.infos.path,
    component: slideshow
  })
})
// routes.push({ path: '*', component: Home })
routes.push({ path: '*', component: Homepage })
// routes.push({ path: '/introduce', component: Homepage })

let router = new Router({
  mode: 'history',
  routes
})

new Vue({
  el: '#app',
  router,
  render: h => h('App'),
  components: { App }
})
